.snake{
	width:600px;
	height:600px;
	position:relative;
}

.snake-body,.snake-food{
	display:inline-block;
	width:19px;
	height:19px;
	box-sizing: border-box;
	margin:0px 1px 1px 0px;
	position:absolute;
}

.snake-body{
	background-color:green;
}

.snake-food{
	background-color: #DE2FA7;
}

